/* MAIN */
:root {
  --font-family: "Roboto", sans-serif;
  --second-family: "Font Awesome 5 Free", sans-serif;
  --third-family: "SF UI Text", sans-serif;
}

body {
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  display: flex;
  align-items: stretch;
  flex-direction: column;
  margin: 0;
  min-height: 100vh;
}

a {
  text-decoration: none;
}

.smallheader-margin {
  box-sizing: border-box;
  margin-top: -112px;
  z-index: 2;
}

.defaultheader-margin {
  box-sizing: border-box;
  margin-top: -182px;
  z-index: 2;
}

.profileheader-margin {
  box-sizing: border-box;
  margin-top: -224px;
  z-index: 2;
}

.parentbox {
  display: flex;
  justify-content: center;
}

.childbox {
  max-width: 1440px;
}

.subtitle {
  cursor: default;
  user-select: none;
}

.window-container {
  overflow: hidden;
  position: fixed;
  top: 50%;
  left: 50%;
  height: 100%;
  width: 100%;
  background: transparent;
  opacity: 0;
  z-index: 5;
  transform: translate(-50%, -50%) scale(0%);
  transition:
    transform 0s 0.2s,
    opacity 0.2s;
}

.window-container.visible {
  opacity: 1;
  transform: translate(-50%, -50%) scale(100%);
  transition:
    transform 0.2s,
    opacity 0s;
}

.window-background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(8px);
}

.window-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.window-content-close {
  position: absolute;
  height: 28px;
  width: 28px;
  background-image: url(../res/img/icons/x-mark.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  user-select: none;
}

